/* Section Location */

.location__content {
	padding-top: 226px;
	padding-bottom: 99px;
	overflow: hidden;

	&--landscape,
	&--hotel {
		height: 700px;
		width: 100%;
		background-repeat: no-repeat;
		background-size: cover;
	}

	&--hotel {
		background-image: url("./../img/GrandSpainMadrid-1.jpg");
	}

	&--landscape {
		background-image: url("./../img/GrandSpainMadrid-2.jpg");

		& .location__card {
			margin-left: auto;
			margin-right: 0;

			// Mobile XS
			@media (max-width: 599px) {
				margin-left: unset;
			}
		}
	}

	// Tablet
	@media (max-width: 1199px) {
		padding-top: 100px;
		padding-bottom: 100px;
	}

	// Mobile
	@media (max-width: 899px) {
		padding-top: 50px;
		padding-bottom: 50px;
		height: auto;
	}
}

.location__card {
	width: 543px;
	padding: 50px 32px;
	background: rgba(51, 51, 51, 0.9);

	h4 {
		margin-bottom: 23px;
		font-weight: 400;
		font-size: 20px;
		letter-spacing: 1.5px;
	}

	h3 {
		margin-bottom: 13px;
		font-weight: 400;
		font-size: 20px;
		color: $white;
	}

	p + p {
		margin-top: 20px;
	}

	p:last-of-type {
		margin-bottom: 46px;
	}

	// Mobile
	@media (max-width: 899px) {
		padding: 30px 32px;
		font-size: 14px;

		h3 h3 {
			font-size: 18px;
		}
	}

	// Mobile XS
	@media (max-width: 599px) {
		width: auto;

		.button {
			width: auto;
		}
	}
}
