/* Header */
.header {
	padding-top: 265px;
	display: grid;

	background: url("./../img/mountain.jpg") no-repeat;
	background-size: cover;

	// Tablet
	@media (max-width: 1199px) {
		padding-top: 0;
		padding-bottom: 100px;

		& .container {
			order: 2;
		}
	}

	// Mobile
	@media (max-width: 899px) {
		padding-bottom: 50px;
	}
}

.header__nav  {
	padding-top: 30px;
	padding-bottom: 30px;
	margin-top: 132px;
	height: 80px;

	background: $blue;

	// Tablet
	@media (max-width: 1199px) {
		order: 1;
		margin-top: 0;
		margin-bottom: 265px;
		padding: 20px;
		height: 60px;
	}

	// Mobile
	@media (max-width: 899px) {
		margin-bottom: 120px;
		padding-left: 0;
		padding-right: 0;
	}
}

.header__middle {
	margin: 0 auto;
	width: 653px;
	color: $white;
	text-align: center;

	// Mobile
	@media (max-width: 899px) {
		width: 100%;
	}

}

.header__title {
	margin-bottom: 10px;
	font-weight: 700;
	font-size: 48px;
	letter-spacing: 4.8px;
	text-transform: uppercase;

	// Mobile XS
	@media (max-width: 599px) {
		font-size: 36px;
	}
}

.header__subtitle {
	margin-bottom: 48px;
	font-weight: 400;
	font-size: 22px;
	letter-spacing: 6.82px;

	// Mobile XS
	@media (max-width: 599px) {
		font-size: 18px;
	}
}

/* Form */

.header__form {
	margin-bottom: 108px;

	// Mobile
	@media (max-width: 899px) {
		margin-bottom: 80px;
	}
}

.form {
	display: grid;
	grid-template-columns: 1fr 150px;
	justify-content: center;
	transition: 0.3s linear;

	&:hover {
		box-shadow: 0 0 5px $white;
	}

	&:focus {
		box-shadow: 0 0 10px $white;
	}

	// Mobile XS
	@media (max-width: 599px) {
		grid-template-columns: 1fr;
		grid-template-rows: repeat(2, auto);
	}
}

.form__search-input {
	border: 2px solid $white;
	width: 100%;
	padding: 17px;

	text-align: center;
	font-weight: 400;
	font-size: 14px;
	letter-spacing: 2.8px;

	transition: 0.3s linear;
	outline: 0;

	&:placeholder-shown {
		color: $gray;
	}

	&:focus {
		box-shadow: 0 0 15px $white;
	}

	// Mobile
	@media (max-width: 899px) {
		padding: 19px 20px;
		width: 100%;
		letter-spacing: 1.5px;
	}

	// Mobile XS
	@media (max-width: 599px) {
		padding: 19px 10px;
		letter-spacing: 1px;
	}
}

.form__search-button {
	width: auto;
	padding-left: 34px;
	padding-right: 34px;

	position: relative;
	border: 2px solid #ffffff;
	text-align: left;

	transition: 0.3s linear;
	outline: 0;

	span {
		&:after {
			position: absolute;
			content: "";
			width: 22px;
			height: 24px;
			top: -6px;
			right: -30px;
			background-image: url("../img/icons/search.svg");
			background-repeat: no-repeat;
		}
	}

	&:hover {
		box-shadow: 0 0 5px $white;
		background: $light-blue;
	}

	&:focus {
		border: 2px solid $white;
		background: $light-blue;

	}

	// Mobile XS
	@media (max-width: 599px) {
		text-align: center;
	}
}
