/* Navigation */
.nav__wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.nav {
	display: flex;
	align-items: center;
	justify-content: space-between;

	@media (max-width: 899px) {
		display: none;
		top: -100%;
		opacity: 0;
	}
}

.nav__list {
	display: flex;

	li:not(:last-child) {
		margin-right: 36px;
	}

	a {
		font-family: "Aleo";
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		text-transform: uppercase;
		text-decoration: none;
		color: $white;

		outline: 0;
		transition: 0.3s linear;

		&:hover {
			text-shadow: 0 0 5px $white;
			text-decoration: underline;
		}

		&:focus {
			text-decoration: underline;
			text-shadow: 0 0 10px $white;
		}
	}

	// Mobile
	@media (max-width: 899px) {
		display: none;

		li:not(:last-child) {
			margin-right: 0;
		}
	}
}

.nav.nav--active {
	// Mobile
	@media (max-width: 899px) {
		z-index: 9;
		position: fixed;
		left: 0;
		top: 0;
		height: 100vh;
		width: 100%;

		background-color: $blue;
		opacity: 1;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		transition: top 1.7s linear, opacity 1.7s linear;

		* + * {
			margin-top: 70px;
		}

		.nav__list {
			display: block;
			color: white;
			text-align: center;
			font-size: 34px;

			* + * {
				margin-top: 20px;
			}
		}
	}
}
