/* Logo */

.logo {
	display: block;
	font-weight: 700;
	font-size: 24px;
	letter-spacing: 2.4px;
	text-transform: uppercase;
	color: $white;
	text-decoration: none;

	outline: 0;
	transition: 0.5s linear;

	&:hover {
		text-shadow: 0 0 5px $white;
	}

	&:focus {
		text-shadow: 5px 5px 10px $white;
	}
}

.header__logo {
	&:hover {
		text-shadow: 0 0 5px $dark-blue;
	}

	&:focus {
		text-shadow: 5px 5px 10px $dark-blue;
	}
}

.header__logo--active {
	z-index: 10;
	position: fixed;
	top: 20px;
	left: 15px;
}

.logo-wrap {
	transition: 0.3s linear;

	& .logo:hover {
		transform: scale(1.1, 1.1);
	}

	& .logo:focus {
		transform: scale(1.1, 1.1);
	}
}
