/* To Top */

.to-top {
	z-index: 8;
	position: fixed;
	display: block;
	bottom: 15px;
	right: 15px;

	border-radius: 50%;
	opacity: 0.5;

	transition: 0.3s linear;
}

.fa-chevron-circle-up {
	font-size: 40px;
	color: $gray;

	&:hover {
		color: $white;
		text-shadow: 0 0 10px $white;	
	}
	
	&:focus {
		color: $white;
	}
}
