// Ширина и высота линии в иконке
$lineWidth: 20px;
$lineHeight: 3px;
$radius: 2px;
$lineMargin: 4px;
$color: $blue;

// Размеры контейнера
// $width: $lineWidth;
// $height: $lineHeight * 3 + $lineMargin * 2;

$width: 40px;
$height: 40px;

.nav-icon {
	z-index: 10;
	display: none;

	&.nav-icon--active {
		position: fixed;
	}

	// Mobile
	@media (max-width: 899px) {
		position: absolute;
		top: 10px;
		right: 15px;
		width: $width;
		height: $height;

		display: flex;
		justify-content: center;
		align-items: center;

		background-color: $white;
		border-radius: 50%;
		border: solid 1px $white;

		outline: 0;
		transition: 0.2s ease-in;
		cursor: pointer;

		&:hover,
		&:focus {
			box-shadow: 0 0 5px $white;
			background-color: $blue;
			border: solid 1px $white;

			.nav-icon__middle,
			.nav-icon__middle::before,
			.nav-icon__middle::after {
				background-color: $white;
			}
		}
	}

	// Mobile XS-319
	@media (max-width: 280px) {
		display: none;
	}
}

.nav-icon__middle {
	position: relative;
	width: $lineWidth;
	height: $lineHeight;
	background-color: $color;
	border-radius: $radius;
	transition: 0.4s ease-in-out;

	&::before,
	&::after {
		content: "";
		position: absolute;
		left: 0;
		width: $lineWidth;
		height: $lineHeight;
		transition: transform 0.2s ease-in-out, top 0.3s linear,
			background-color 0.2s ease-in-out;
		background-color: $color;
		border-radius: $radius;
	}

	&::before {
		top: -($lineHeight + $lineMargin);
	}

	&::after {
		top: $lineHeight + $lineMargin;
	}
}

/* Active state */

.nav-icon.nav-icon--active .nav-icon__middle {
	z-index: 99;
	position: fixed;
	background-color: transparent;

	&::before,
	&::after {
		top: 0;
		transition: top 0.2s linear, transform 0.4s ease-in;
	}

	&::before {
		transform: rotate(45deg);
	}

	&::after {
		transform: rotate(-45deg);
	}
}
